.tablecard {
  min-height: 50vh;
  margin: 0.5em 0;
}

.table {
  border-collapse: collapse;
  width: 100%;
  height: 100%;
}

.table th, .table td {
  border: 1px solid #dddddd;
  text-align: center;
  padding: 6px;
}

.table th {
  background-color: #ebebeb;
}

.horizontal {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.searchbar {
  width: 100%;
  height: 100%;
  display: flex;
  margin: 0.5em 0;
}

.searchbar-icon {
  margin-left: 0.2em;
  margin-right: 0.4em;
}

.searchbar-letters {
  font-size: large;
}